var MyApp = {
    carruselPlanes: function(){

        $('.plan__footer .btn-flat').bind('click', function(){
            $('.plan__item').toggleClass('item--open');
            $('.benefit__list--compress').slideToggle();
            $('.benefit__list--complete').slideToggle();
        });
        
        $('.plans .plans__list').slick({        
            slidesToShow: 4,
            slidesToScroll: 1,
            dots: false,
            arrows: true,
            responsive: [
                {
                  breakpoint: 1350,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 1,                
                        dots: false,
                    }
                },
                {
                  breakpoint: 1199,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 1,
                        dots: true,
                        arrows: false
                    }
                },
                {
                  breakpoint: 992,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 1,
                        dots: true,
                        arrows: false
                    }
                },
                {
                  breakpoint: 650,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1,
                        dots: true,
                        arrows: false
                    }
                }
                
            ]
        })
    
    },
    contactanos: function(){

        //Select materialize
        var $select = $('select');
        $select.material_select();

        //Funcion de intervalo de hora para el campo select
        function verifyHourDisplay(){
            var hourEnableData = '22:00'; //'22:00'
            var hourDisabledData = '06:00'; //'06:00'
            var hourEnableArray = hourEnableData.split(':');
            var hourDisabledArray = hourDisabledData.split(':');
            var hourEnable = parseInt(hourEnableArray[0]);
            var minuteEnable = parseInt(hourEnableArray[1]);
            var hourDisabled = parseInt(hourDisabledArray[0]);
            var minuteDisabled = parseInt(hourDisabledArray[1]);

            if((hourEnable + minuteEnable) > 0 && (hourDisabled + minuteDisabled) > 0){

                var dateNowBn = new Date();
                var isShow = true;
                if(dateNowBn.getHours() <= hourEnable && dateNowBn.getHours() >= hourDisabled){
                    if(dateNowBn.getHours() == hourEnable || dateNowBn.getHours() == hourDisabled){
                        if((dateNowBn.getHours() == hourEnable && dateNowBn.getMinutes() < minuteEnable) || (dateNowBn.getHours() == hourDisabled && dateNowBn.getMinutes() >= minuteDisabled)){
                            isShow = false;
                        }
                    }else{                        
                        isShow = false;
                    }                    
                }
                
                if(isShow){
                    $('#hour').parent().parent().removeClass('disabled');
                    active();
                }else{
                    var $select = $('#hour')
                    $select.prop('selectedIndex', 0);
                    $select.material_select();
                    active();
                    $('#hour').parent().parent().addClass('disabled');
                }
            }
        }
        verifyHourDisplay();
        setInterval(verifyHourDisplay, 500);
        
        //casos drop down Select
        $(".field__select input[type=text]").blur(function(){
            $('.icon-drop_down').removeClass('active');
        })
        $(".field__select input[type=text]").on('click', function(){
            if($(this).hasClass('active')){
                $('.icon-drop_down').addClass('active');
            }else{
                $('.icon-drop_down').removeClass('active');
            }
        })
        $(".field__select .dropdown-content li").on('click', function(){
            $('.icon-drop_down').removeClass('active');
        });
        $('#termsCondition').on('click', function(){
            $('.field__checkbox').prop("checked", true);
        });

        //Select textos de horario para el modal
        $('#hour').on('change', function(e){
            var $value = $('.dropdown-content li.active').text();
            $('.description--hour').text($value);
        });

        //modales
        $('#formContact').modal({
            dismissible: false,
            ending_top: '10%',    
        });
        $('#movil').modal({
            dismissible: false,
            ending_top: '10%',         
        });
        $('#error,#success,#success-2').modal({
            dismissible: false,
        });
        $('#protectClause').modal({
            dismissible: false,
            ready: function(modal, trigger) { // Callback for Modal open. Modal and trigger parameters available.                
                //Scroll content Terminos y condiciones
                var swiper = new Swiper('#protectClause .swiper-container', {
                    direction: 'vertical',
                    slidesPerView: 'auto',
                    freeMode: true,
                    scrollbar: {
                    el: '.swiper-scrollbar',
                    },
                    mousewheel: true,
                })
            },
        })

        //Especificaiones de campo
        var $document = $('.contact__modal  #document');
        var $phone = $('.contact__modal  #phone');      

        $phone.on("keypress", function(e) {
            var numTotal = this.value.length;
            key = e.keyCode || e.which;
            if (numTotal == 0) {
                var tecla = String.fromCharCode(key);
                var caracteres = "9";
                var especiales = [8, 9, 37, 39, 46, 6];
                var tecla_especial = false;
                for (var i in especiales) {
                    if (key == especiales[i]) {
                        tecla_especial = true;
                        break;
                    }
                }
                if (caracteres.indexOf(tecla) == -1 && !tecla_especial) {
                    return false;
                }
            }
            return (key > 47 && key < 58);
        });

        $document.on('input', function () { 
            this.value = this.value.replace(/[^0-9]/g,'');
        });
        
        //Titulos modal form
        $('.contact__item').on('click', function(){
            var text = $(this).find('.contact__btn .description').text();
            var replaceText = text.toLowerCase().split(" ").join("-");
            var resetText = replaceText.normalize("NFD").replace(/[\u0300-\u036f]/g, "")
            $('.contact__modal .modal__title strong').text(text);
            $('#formContact').attr('data-modal',resetText);
        });

        //Atributos modales movil
        $('#movil .modal__btn').on('click', function(){
            var text = $(this).text();
            var replaceText = text.toLowerCase().split(" ").join("-");
            var resetText = replaceText.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
            $('#formContact').attr('data-modal',resetText);
        });        

        //Valid and enabled button
        $('.field__btn').attr('disabled', true);

        function doctype(){
            $('#document').on('keyup', function(){
                var option = 1;
                $(this).parent().removeClass('field__invalid');
                if($(this).val().length < 8){
                    option = 0;
                    $(this).parent().addClass('field__invalid');
                }
                setTimeout(function(){
                    active(1,option);
                },5)
            });
        }
        function phone(){
            $('#phone').bind('keyup', function(){
                var option = 1;
                $(this).parent().removeClass('field__invalid');
                if($(this).val().length < 9){
                    option = 0;
                    $(this).parent().addClass('field__invalid');
                }                
                setTimeout(function(){
                    active(2,option);
                },5)
            });
        }
        function hour(){
            $('#hour').on('change', function(e){
                var select = e.target.value;
                var option = 1;
                $(this).parent().removeClass('field__invalid');
                if(select == 0){
                    option = 0;
                    $(this).parent().addClass('field__invalid');
                }
                setTimeout(function(){
                    active(3,option);
                },5)
            });
        }
        function terms(){
            $('#treatment, #termsCondition').on('click', function(){
                var option = 1
                if($(this).prop("checked") == false){
                    option = 0;
                }
                setTimeout(function(){
                    active(4,option);
                },5)
            });
        }           

        var count = 0, ndocument = 0, nphone = 0, nhours = 0, nterms = 0;

        function active(status,option){
            if( status === 1) { ndocument = option }
            if( status === 2) { nphone = option }
            if( status === 3) { nhours = option }
            if( status === 4) { nterms = option }

            if($('#treatment').hasClass('required')){
                if($('.field__group').hasClass("disabled")){
                    count = parseInt(ndocument) + parseInt(nphone) + parseInt(nterms);
                    if(count === 3){
                        $('.field__btn').attr('disabled',false);
                    }else{
                        $('.field__btn').attr('disabled',true);
                    }                
                }else{
                    count = parseInt(ndocument) + parseInt(nphone) + parseInt(nhours) + parseInt(nterms);
                    if(count === 4){
                        $('.field__btn').attr('disabled',false);
                    }else{
                        $('.field__btn').attr('disabled',true);
                    }
                }
            }else{
                if($('.field__group').hasClass("disabled")){
                    count = parseInt(ndocument) + parseInt(nphone);
                    if(count === 2){
                        $('.field__btn').attr('disabled',false);
                    }else{
                        $('.field__btn').attr('disabled',true);
                    }                
                }else{
                    count = parseInt(ndocument) + parseInt(nphone) + parseInt(nhours);
                    if(count === 3){
                        $('.field__btn').attr('disabled',false);
                    }else{
                        $('.field__btn').attr('disabled',true);
                    }
                }
            }                        
        }
        
        //Envio de Formulario
        $( ".contact__modal form" ).on('submit', function( event ) {
            event.preventDefault();
            //Limpieza de campos y variables
            $('#document').val('');
            $('#phone').val('');
            var $select = $('#hour').prop('selectedIndex', 0);
            $select.material_select();
            $('#treatment').prop('checked', false);
            $('.field__btn').attr('disabled', true);
            count = 0;
            ndocument = 0;
            nphone = 0;
            nhours = 0;
            nterms = 0;
            //Evento Modales
            $('#formContact').modal('close');
            if($('.field__group').hasClass("disabled")){
                $('#success').modal('open');
            }else{
                $('#success-2').modal('open');
            }
        }); 

        doctype();
        phone();
        hour();
        terms();
        active();
    },

    servicio: function(){                

        //Funcion Condicional del checkbox
        function isCheck(campo){
            var selfList = campo.parent().parent().find('.service__slick');
            if ((campo.is(':checked')) && ($(window).outerWidth() <= 600)) {
                console.log(true);
                $(selfList).slick('unslick');
                $(selfList).addClass('service__unslick');            
                $('.service__unslick').parent().parent().find('.service__footer').fadeIn();
            }else{
                $('.service__unslick').parent().parent().find('.service__footer').fadeOut();
                $('.service__unslick').each(function(i){
                    $(this).slick('refresh');
                    $(this).removeClass('service__unslick');
                });                             
            }            
        }
        
        $('.service__list').each(function(){
            var listSlick = $(this);
            var itemSlick = listSlick.find('.service__item');
            if(listSlick.children().length > 4){
                listSlick.addClass('service__slick');
            }
            itemSlick.each(function(e){
                if(e > 3){
                    $(this).addClass('itemToggle');
                }
            });
        });
        
        $('.service__slick').slick({
            autoplay: false,
            dots: true,
            arrows: true,
            infinite:  false,
            speed: 300,
            slidesToShow: 3,
            slidesToScroll: 3,
            focusOnSelect: false,
            responsive: [
                {
                breakpoint: 992,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 1,            
                    }
                },
                {
                breakpoint: 600,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1,
                    }
                }
            ]
        });            

        $('.filled-in').on('click', function(){          
            var $self = $(this);
            $(window).on('resize', function(){
                isCheck($self);
            });
            $(window).resize();
        });
        
        //animacion toggle mostrar servicios
        $('.service__btn').bind('click', function(){
            var item = $(this).parent().parent().parent().find('.itemToggle');
            $(item).slideToggle();
            $(this).toggleClass('displayOn');
        });

        $('a.service--anchor').click(function(){
            var $selfAnchor = $(this).attr("href"); 
            window.location.hash = $selfAnchor;
            //animacion de scroll section
            $("html, body").animate({ scrollTop: $('section[data-anchor='+$selfAnchor+']').offset().top }, 500);
        });

    },
    equipos: function(){
        var swiper = new Swiper('.equipment--carousel .swiper-container', {
            effect: 'coverflow',
            slidesPerView: 3,
            spaceBetween: -90,
            centeredSlides: true,
            loop: true,
            coverflowEffect: {
                rotate: -80,
                stretch: -55,
                depth: 160,
                modifier: 1,
                slideShadows : false,
            },
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            },
            breakpoints: {
                600: {
                    spaceBetween: 35,
                    coverflowEffect: {
                        rotate: -30,
                        stretch: 5
                    }
                }
            }
          });
        var swiper2 = new Swiper('.equipment--block .swiper-container',{            
            centeredSlides: true,
            slidesPerView: 1,
            spaceBetween: 5,
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            },
            pagination: {
                el: '.swiper-pagination',
            }
        });
    },
    video: function(){
        
        function heightTumbs(){
            var $selfAlto = $('.display__item').outerHeight();
            $('.thumbs__list').css('max-height', $selfAlto);                    
        }

        function configVideo(){
            $('.item__video').attr('disablePictureInPicture', 'true').attr('controlsList', 'nodownload');
        }
        
        $('video').bind('webkitfullscreenchange mozfullscreenchange fullscreenchange', function(e) {
            var state = document.fullScreen || document.mozFullScreen || document.webkitIsFullScreen;
            var event = state ? $('.thumbs__list').fadeOut() : $('.thumbs__list').fadeIn();        
        });
        
          $('.display__list').on('Init', function(slick){
            $(slick.$slides).find('.item__video').get(0).play();
          }).slick({
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: false,
            dots: true,
            fade: true,
            asNavFor: '.thumbs__list',
            responsive: [
                {
                    breakpoint: 992,
                    settings:{
                        fade: false,
                        centerMode: true,
                        slidesToShow: 1,
                        infinite: true,
                        variableWidth: true,
                        swipe: true,
                        touchMove: true,
                        swipeToSlide: true,
                        focusOnSelect: true,
                    }
                },
            ]
          }).on('beforeChange', function(event, slick, currentSlide, nextSlide){
            $(slick.$slides[currentSlide]).find('.item__video').get(0).pause();
          });
          $('.thumbs__list').slick({
            vertical: true,
            verticalSwiping: true,
            slidesToShow: 3,
            slidesToScroll: 1,
            infinite:true,
            asNavFor: '.display__list',
            dots: false,
            centerMode: true,
            centerPadding: '0',
            focusOnSelect: true,
          });

          $(document).on('click', 'video', function(){
            if (this.paused) {                
                $(this).parent().find('.item__poster').fadeOut();
                $(this).parent().find('.item__icon').fadeOut();
            }            
          });   
          
          $(".display__item").each(function(){

            //Carga diferida de videos
            var selfVideo = $(this).find('.item__video');
            var sourceVideo = selfVideo.find('source')
            var sourceFile = sourceVideo.data('src');
            sourceVideo.attr("src", sourceFile);
                       
        });
    
        $('.display__list .item__icon').on('click', function(){
            var $self = $(this);
            var $parent = $self.parent();
            $self.fadeOut();
            $parent.find('.item__poster').fadeOut();
            $parent.find('.item__video').get(0).play();
        });        

        $(window).load(function() {
            configVideo();
            heightTumbs();
            $('.video__gallery >.container').css({
                opacity: 1
            }, 500)
            // console.log($('.item__video').get(0).duration)      
        });

        $(window).on('resize', function(){            
            heightTumbs();
        });

        window.addEventListener("orientationchange", function() {
            if(screen.orientation.angle == 90){
                $('.slider-nav').slick('refresh');
            }else{
                $('.slider-nav').slick('unslick');
            }
        });
    }
}
$(function () {
    MyApp.carruselPlanes();
    MyApp.contactanos();
    MyApp.servicio();
    MyApp.equipos();
    MyApp.video();
});

